.info-page h5 {
  font-weight: 600; }

.info-page .no-margin {
  margin-left: 0;
  margin-right: 0;
  padding: 0 !important;
  max-width: none; }

.info-page .background-overlay {
  position: relative; }
  .info-page .background-overlay .overlay {
    width: 100vw;
    object-fit: contain;
    mix-blend-mode: multiply;
    will-change: opacity; }

.info-page .content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: left; }
  .info-page .content p,
  .info-page .content h5 {
    margin: 0; }
  .info-page .content h1 {
    margin-bottom: 10px; }
  .info-page .content h5 {
    margin-bottom: 20px; }

.info-page .main-img {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-left: 37px; }
  @media screen and (max-width: 991px) {
    .info-page .main-img {
      padding-left: 0; } }
  .info-page .main-img img {
    width: 100%;
    max-width: 650px;
    height: auto;
    margin: 30px 0; }

.info-page .subscribe-container {
  margin: 20px auto 0 auto;
  transform: translateY(185px);
  background: #58C1A4;
  box-shadow: 0px 40px 30px rgba(0, 0, 0, 0.2);
  border-radius: 20px;
  min-height: 277px;
  position: relative;
  z-index: 2;
  max-width: 1200px; }
  @media screen and (max-width: 767px) {
    .info-page .subscribe-container {
      margin-top: -250px;
      transform: translateY(320px); } }
  @media screen and (max-width: 575px) {
    .info-page .subscribe-container {
      min-height: 0;
      padding: 20px; } }
  .info-page .subscribe-container .subscribe-main-img {
    width: auto;
    position: absolute;
    bottom: 0;
    left: 55px; }
    @media screen and (max-width: 980px) {
      .info-page .subscribe-container .subscribe-main-img {
        max-width: 300px;
        left: 25px; } }
    @media screen and (max-width: 767px) {
      .info-page .subscribe-container .subscribe-main-img {
        max-width: 250px;
        left: 25px; } }
    @media screen and (max-width: 640px) {
      .info-page .subscribe-container .subscribe-main-img {
        max-width: 200px;
        left: 10px; } }
  .info-page .subscribe-container .right-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    padding-left: 10px; }
    @media screen and (max-width: 575px) {
      .info-page .subscribe-container .right-container {
        padding: 0;
        align-items: center; } }
    @media screen and (min-width: 630px) {
      .info-page .subscribe-container .right-container {
        padding-left: 50px; } }
    .info-page .subscribe-container .right-container h3 {
      font-weight: bold;
      font-size: 26px;
      color: white; }
    .info-page .subscribe-container .right-container h5 {
      font-weight: normal;
      font-size: 14px;
      color: white;
      max-width: 270px;
      margin-bottom: 25px; }
    .info-page .subscribe-container .right-container .store-btn {
      width: auto;
      height: 45px;
      transform: translateX(-8px); }
    .info-page .subscribe-container .right-container a {
      padding: 2px; }
      .info-page .subscribe-container .right-container a img {
        transition: transform 0.3s ease;
        transform-origin: center; }
        .info-page .subscribe-container .right-container a img:hover {
          transform: translateX(-8px) scale(1.05); }
        .info-page .subscribe-container .right-container a img:active {
          transform: translateX(-8px) scale(1); }

.info-page-margin {
  max-width: 1110px;
  padding: 0 40px;
  margin: auto;
  width: 100%; }
  @media screen and (max-width: 991px) {
    .info-page-margin {
      padding: 0 15px; } }
